import React, { Component } from 'react'

class Test extends Component {
    constructor(props) {
        super(props)
        this.state = {
            showImage: false,
            loader: '../../../public/images/2.jpg',
        }
    }
    componentDidMount() {
        let src = '../../../public/images/23.jpg'
        let img = new Image();
        img.src = src;
        img.onload = () => { //图片下载完毕时异步调用callback函数。 
            console.log('ok');
            this.setState({
                loader: src,
            })
        };
    }
    render() {
        let { loader } = this.state
        return (
            <div className='warper'>
                <div className="header">
                    <div className="h1">个人中心</div>
                </div>
                <div className="content">
                    <div style={{ height: '300px', background: 'url(' + loader + ')' }}>4444</div>
                    <div style={{ height: '300px' }}>2222</div>
                    <div style={{ height: '300px' }}>3333</div>
                    <div style={{ height: '300px', background: 'url(../../../public/images/2.jpg)' }}>4444</div>
                </div>
            </div>
        )
    }
}

export default Test


